.shop-row {
    min-height: calc(100% - 100px);
    min-height: calc(100vh - 100px);
    max-height: calc(100% - 100px);
    max-height: calc(100vh - 100px);
    overflow-y: scroll;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,.70)), to(rgba(255,255,255,.70))), url("../images/techbackground.jpg");
    background: -webkit-linear-gradient(rgba(255,255,255,.70), rgba(255,255,255,.70)), url("../images/techbackground.jpg");
    background: -o-linear-gradient(rgba(255,255,255,.70), rgba(255,255,255,.70)), url("../images/techbackground.jpg");
    background: linear-gradient(rgba(255,255,255,.70), rgba(255,255,255,.70)), url("../images/techbackground.jpg");
    background-size: 100% 100%;
    margin-top: 50px;
    padding-bottom: 15px;
}

.shop-text {
    text-align: center;
    font-weight: bold;
    font-size: calc(1.2vw + 1.2vh);
}


